html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
  display: block;
}

.search-form {
  justify-content: center;
  gap: 10px;
  margin: 0 auto 15px;
  display: flex;
}

.gallery {
  width: 1310px;
  height: 88vh;
  flex-wrap: wrap;
  margin: auto;
  display: flex;
  overflow-y: scroll;
}

.photo-card {
  width: 300px;
  border: .8px solid gray;
  flex-direction: column;
  padding: 5px 5px 0;
  display: flex;
}

.photo-card:not(:nth-child(4n)) {
  margin-right: 16px;
}

.photo-card:not(:nth-last-child(-n+4)) {
  margin-bottom: 12px;
}

.photo-card__img-thumb {
  max-width: 280px;
  max-height: 220px;
  border: .1px solid rgba(58, 58, 58, .23);
  margin: auto;
}

.photo-card__info {
  justify-content: space-between;
  gap: 5px;
  padding: 0 7px;
  display: flex;
}

.load-more-btn {
  margin: 10px auto 0;
  display: block;
}

